<template>
  <el-dialog :visible="dialog" @close="close"
             custom-class="common-dialog"
             width="630" :before-close="beforeClose">
    <div class="dialog-header">
      <i class="el-icon-caret-right icon"></i>

      <h2 class="title">查询</h2>

      <img src="../../../assets/img/close.png" class="close" @click="close">

    </div>
    <div class="dialog-form">
      <el-form label-width="100px">

        <el-form-item label="排班时间" class="one-line-item">
          <el-date-picker v-model="form.SubmitTime" type="datetime"
                          placeholder="选择排班时间"
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="操作员" class="one-line-item">
          <el-select v-model="form.OperatorID" placeholder="操作员" @change="getAisleCodeAndArea">
            <el-option v-for="(item, index) in userList" :key="index"
                       :label="`${item.UserID}-${item.RealName}`" :value="item.UserID">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="指廊" class="one-line-item">
          <el-input v-model.trim="form.AisleCode" placeholder="指廊" disabled></el-input>
        </el-form-item>


        <el-form-item label="负责区域" prop="Area" class="one-line-item">
          <el-select v-model="form.Area" placeholder="请选择负责区域" multiple>
            <el-option :label="item.BridgeCode" :value="item.BridgeCode"
                       v-for="(item,index) in AreaList" :key="index">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="岗位属性" prop="Attribute" class="one-line-item">
          <el-select v-model="form.Attribute" placeholder="请选择岗位属性">
            <el-option label="固定" value="regular"></el-option>
            <el-option label="机动" value="reserve"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="上班时间" prop="StartTime" class="one-line-item">
          <el-date-picker v-model="form.StartTime" type="datetime"
                          placeholder="选择上班时间"
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="下班时间" prop="StopTime" class="one-line-item">
          <el-date-picker v-model="form.StopTime" type="datetime"
                          placeholder="选择下班时间"
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="调度员" prop="DispatcherID" class="one-line-item">
          <el-input v-model.trim="form.DispatcherID" placeholder="调度员"></el-input>
        </el-form-item>

      </el-form>

      <el-button type="primary" class="from-confirm-btn" @click="toSearch">
        查询
      </el-button>
    </div>

  </el-dialog>
</template>
<script>
  import {mapState} from 'vuex'
  import {doCopyObj} from '../../../utils/public'
  import {dispatchService} from '../../../http/service'
  import {initSchedulingField} from '../../../http/filed'

  export default {
    props: ['getScheduling', 'userList'],
    computed: {
      ...mapState(['dialog'])
    },
    data() {
      return {
        form: {
          Area: []
        },
        AreaList: []
      }
    },
    methods: {
      // 1、获取操作员对应的指廊和负责区域
      getAisleCodeAndArea() {
        dispatchService.initScheduling({
          params: initSchedulingField(this.form.OperatorID),
          cb: (res) => {
            this.AreaList = res
            if (res.length > 0) {
              this.form.AisleCode = res[0].AisleCode
              this.form = doCopyObj(this.form)
            } else {
              this.form.AisleCode = ''
              this.form = doCopyObj(this.form)
            }
          }
        })
      },
      // 1、关闭窗口1
      beforeClose() {
        this.setDialog(false)
      },
      // 2、关闭窗口2
      setDialog(dialog) {
        this.$store.dispatch('setDialog', dialog)
      },
      // 3、关闭窗口3
      close() {
        this.setDialog(false)
      },
      toSearch() {
        this.getScheduling(this.form)
        this.setDialog(false)
      }
    }
  }
</script>
