<template>
  <el-dialog :visible="dialog" @close="close"
             custom-class="common-dialog"
             width="1088px" :before-close="beforeClose">
    <div class="dialog-header">
      <div class="notice1" :class="{'no-active':!checked}">
        <el-button type="primary" class="notice-btn" icon="fa fa-bell" size="medium" @click="checked=true">标注
        </el-button>
        <span class="num">1</span>
      </div>
      <div class="notice2" :class="{'no-active':checked}">
        <el-button type="primary" class="notice-btn" icon="el-icon-s-comment" size="medium" @click="checked=false">标注
        </el-button>
        <span class="num">4</span>
      </div>

      <div class="search-item" v-show="checked">
        <span class="txt">类型来源</span>
        <el-select v-model="searchForm.command" placeholder="请选择类型来源">
          <el-option label="001" value="001"></el-option>
        </el-select>
      </div>

      <el-button type="primary" class="notice-btn-right" v-show="!checked">
        一键标记为已读
      </el-button>

      <img src="../../../assets/img/close.png" class="close" @click="close">

    </div>
    <div class="dialog-table" v-show="checked">
      <el-table :data="tableData1" border height="390px">
        <el-table-column type="index" width="50" label="序号" align="center" ></el-table-column>
        <el-table-column v-for="(item,index) in tableItem1" :key="index"
                         :prop="item.prop"
                         :label="item.name"
                         align="center">
        </el-table-column>
        <el-table-column label="操作" align="center" width="220px">
          <template slot-scope="scope">
            <el-button type="primary" class="table-handle-btn1">关闭警铃</el-button>
            <el-button type="primary" class="table-handle-btn2">撤销警铃</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page-handle">
        <el-pagination
            background small
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="page.num" :page-size="page.size"
            layout="prev, pager, next, jumper"
            :total="page.total">
        </el-pagination>
        <el-button class="page-handle-btn" @click="handleCurrentChange">确定</el-button>
      </div>
    </div>
    <div class="dialog-table" v-show="!checked">
      <el-table :data="tableData2" border height="390px">
        <el-table-column type="index" width="50" label="序号" align="center" ></el-table-column>
        <el-table-column v-for="(item,index) in tableItem2" :key="index"
                         :prop="item.prop"
                         :label="item.name"
                         align="center">
        </el-table-column>
        <el-table-column label="操作" align="center" width="220px">
          <template slot-scope="scope">
            <el-button type="primary" class="table-handle-btn1">标记为已读</el-button>
            <el-button type="primary" class="table-handle-btn4">已读</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page-handle"></div>
    </div>


  </el-dialog>
</template>
<script>
  import {mapState} from 'vuex'

  export default {
    props: ['isChecked'],
    computed: {
      ...mapState(['dialog'])
    },
    data() {
      return {
        checked: true,
        searchForm: {},
        tableItem1: [
          {prop: "date1", name: "时间"},
          {prop: "date2", name: "类型来源"},
          {prop: "date3", name: "事件"},
          {prop: "date4", name: "桥号"},
          {prop: "date5", name: "部门"},
          {prop: "date6", name: "操作员"},
          {prop: "date7", name: "计时"}
        ],
        tableData1: [
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'}
        ],
        page: {num: 1, size: 10, total: 1000},
        tableItem2: [
          {prop: "date1", name: "时间"},
          {prop: "date2", name: "类型"},
          {prop: "date3", name: "事件"},
          {prop: "date4", name: "桥号"},
          {prop: "date6", name: "操作员"},
          {prop: "date7", name: "计时"}
        ],
        tableData2: [
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'},
          {date1: 'date1', date2: 'date1', date3: 'date1', date4: 'date1', date5: '1', date6: 'date1', date7: 'date1'}
        ]
      }
    },
    methods: {
      // 1、关闭窗口1
      beforeClose() {
        this.setDialog(false)
      },
      // 2、关闭窗口2
      setDialog(dialog) {
        this.$store.dispatch('setDialog', dialog)
      },
      // 3、关闭窗口3
      close() {
        this.setDialog(false)
      },
      handleSizeChange(val) {

      },
      handleCurrentChange(val) {

      }
    },
    watch: {
      isChecked() {
        this.checked = this.isChecked
      }
    }
  }
</script>
<style scoped lang="less">
  .common-dialog {
    .notice1 {
      margin: 0 30px 0 10px;
      position: relative;
    }

    .notice2 {
      position: relative;
    }

    .num {
      position: absolute;
      top: 6px;
      left: 30px;
      width: 12px;
      height: 12px;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      color: #fff;
      background: #da1a05;
      border-radius: 50%;
    }

    .no-active {
      .notice-btn {
        background: #d0d0d0;
        border-color: #d0d0d0;
      }

      .num {
        background: #229af9;
      }
    }

    .notice-btn-right {
      position: absolute;
      right: 64px;
    }
  }
</style>
