<template>
  <div class="scheduling-layout">
    <div class="dispatch-header">
      <el-button type="text" class="text-btn">
        <i class="back"></i>
        返回首页
      </el-button>
    </div>

    <div class="content">

      <div class="table-header">
        <h2 class="title">人员排班</h2>
        <el-button type="primary" class="table-btn1" icon="el-icon-plus" @click="add">新增</el-button>
        <el-button type="primary" class="table-btn2" icon="el-icon-search" @click="toSearch">查询</el-button>
        <el-button type="primary" class="table-btn3" icon="el-icon-download" @click="toDeleteAll">删除</el-button>
      </div>

      <div class="table-content">
        <div class="table-common">
          <el-table :data="tableData" border height="600px" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="40" align="center"></el-table-column>
            <el-table-column v-for="(item,index) in tableItem" :key="index"
                             :prop="item.prop" :label="item.name"
                             align="center" :min-width="item.width">
              <template slot-scope="scope">
                <div v-if="item.prop === 'Attribute'">
                  <span v-if="scope.row.Attribute==='regular'">固定</span>
                  <span v-if="scope.row.Attribute==='reserve'">机动</span>
                </div>
                <div v-else>{{scope.row[item.prop]}}</div>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="center" width="160px">
              <template slot-scope="scope">
                <el-button type="primary" class="table-handle-btn3" @click="edit(scope.row)">编辑</el-button>
                <el-button type="primary" class="table-handle-btn2" @click="toDelete(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="table-sign" @click="toTip">

        </div>
      </div>
    </div>
    <searchScheduling :getScheduling="getScheduling" :userList="userList"></searchScheduling>
    <addScheduling :getScheduling="getScheduling" :userList="userList"></addScheduling>
    <editScheduling :getScheduling="getScheduling" :userList="userList"></editScheduling>
    <deleteScheduling :getScheduling="getScheduling"></deleteScheduling>
    <tipScheduling></tipScheduling>
  </div>
</template>
<script>
  import {searchScheduling, addScheduling, editScheduling, deleteScheduling, tipScheduling} from './pages/index'
  import {adminService, dispatchService} from '../../http/service'
  import {getUserListField, getSchedulingField} from '../../http/filed'

  export default {
    components: {searchScheduling, addScheduling, editScheduling, deleteScheduling, tipScheduling},
    data() {
      return {
        searchForm: {},
        tableItem: [
          {prop: "AisleCode", name: "指廊", width: "5%"},
          {prop: "BridgeCode", name: "桥位号", width: "6%"},
          {prop: "OperatorName", name: "操作员", width: "10%"},
          {prop: "Area", name: "负责区域", width: "9%"},
          {prop: "Attribute", name: "岗位属性", width: "8%"},
          {prop: "StartTime", name: "计划开始时间", width: "15%"},
          {prop: "StopTime", name: "计划结束时间", width: "15%"},
          {prop: "DispatcherName", name: "调度员", width: "8%"},
          {prop: "SubmitTime", name: "排班时间", width: "15%"}
        ],
        tableData: [],
        userList: [],
        deleteArr: []
      }
    },
    mounted() {
      this.onReady()
    },
    methods: {
      // 1、初始化数据
      onReady() {
        // 2、获取航班调度数据
        this.getScheduling()
        this.getUserList()
      },
      // 1、获取操作员列表
      getUserList(form, type) {
        adminService.getUserList({
          params: getUserListField(form, type),
          cb: (res) => {
            this.userList = res.List
          }
        })
      },
      getScheduling(form) {
        dispatchService.getScheduling({
          params: getSchedulingField(form),
          cb: (res) => {
            this.tableData = res.List
          }
        })
      },
      toSearch() {
        this.$store.dispatch('setDialog', true)
      },
      add() {
        this.$store.dispatch('setAddDialog', true)
      },
      edit(row) {
        this.$store.dispatch('setEditDialog', true)
        this.$store.dispatch('setRow', row)
      },
      toDelete(row) {
        this.$store.dispatch('setDeleteDialog', true)
        this.$store.dispatch('setDeleteRow', row.ID + '')
      },
      handleSelectionChange(val) {
        this.deleteArr = []
        val.forEach((v, i) => {
          this.deleteArr.push(v.ID)
        })
      },
      toDeleteAll() {
        this.$store.dispatch('setDeleteDialog', true)
        this.$store.dispatch('setDeleteRow', this.deleteArr)
      },

      toTip(row) {
        this.$store.dispatch('setTipDialog', true)
      }
    }
  }
</script>
<style lang="less" scoped>
  .scheduling-layout {
    .dispatch-header {
      display: flex;
      align-items: center;
      height: 50px;
      background: #fff;
      border-top: 1px solid #f0f1f3;
      position: relative;

      .text-btn {
        position: absolute;
        right: 45px;
        color: #a9b3bf;

        .back {
          display: inline-block;
          width: 16px;
          height: 16px;
          background: url("../../assets/img/back.png");
          vertical-align: middle;
          margin-right: 5px;
        }
      }
    }

    .content {
      .table-header {
        .title {
          font-size: 18px;
          margin-left: 25px;
        }

        .table-btn1 {
          position: absolute;
          right: 230px;
          background: #2f99ff;
          border-color: #2f99ff;
        }

        .table-btn2 {
          position: absolute;
          right: 150px;
          background: #e5af29;
          border-color: #e5af29;
        }

        .table-btn3 {
          position: absolute;
          right: 70px;
          background: #05d2d5;
          border-color: #05d2d5;
        }
      }

      .table-content {
        .table-common {
          height: 600px;
        }
      }

      .table-sign {
        height: 40px;
        border: 0;
      }
    }
  }
</style>
