<template>
  <el-dialog :visible="tipDialog" @close="close"
             custom-class="common-dialog"
             width="520" :before-close="beforeClose">
    <div class="dialog-header">
      <i class="el-icon-caret-right icon"></i>

      <h2 class="title">删除</h2>

      <img src="../../../assets/img/close.png" class="close" @click="close">

    </div>
    <div class="dialog-content">

      <p class="txt">内容已过期，无法编辑或删除</p>
      <div class="dialog-btn">
        <el-button type="primary" class="confirm-btn">
          确定
        </el-button>
      </div>
    </div>

  </el-dialog>
</template>
<script>
  import {mapState} from 'vuex'

  export default {
    props: ['isChecked'],
    computed: {
      ...mapState(['tipDialog'])
    },
    data() {
      return {
        form: {},
        rules: {
          title: [
            {required: true, message: "请输入小于25个字标题", max: 25, trigger: "blur"}
          ],
          op_company_id: [
            {required: true, message: "请选择租户", trigger: "change"}
          ],
          picture: [
            {required: true, message: "请上传图片", trigger: "change"}
          ],
          link_address: [
            {required: true, message: "请填写链接", trigger: "blur"}
          ]
        }
      }
    },
    methods: {
      // 1、关闭窗口1
      beforeClose() {
        this.setTipDialog(false)
      },
      // 2、关闭窗口2
      setTipDialog(tipDialog) {
        this.$store.dispatch('setTipDialog', tipDialog)
      },
      // 3、关闭窗口3
      close() {
        this.setTipDialog(false)
      }
    },
    watch: {
      isChecked() {
        this.checked = this.isChecked
      }
    }
  }
</script>
<style scoped lang="less">
  .common-dialog {

  }
</style>
