<template>
  <el-dialog :visible="editDialog" @close="close"
             custom-class="common-dialog"
             width="630" :before-close="beforeClose">
    <div class="dialog-header">
      <i class="el-icon-caret-right icon"></i>

      <h2 class="title">编辑</h2>

      <img src="../../../assets/img/close.png" class="close" @click="close">

    </div>
    <div class="dialog-form">
      <el-form label-width="100px" :status-icon="true" :model="form"
               :rules="rules" ref="form">

        <el-form-item label="操作员" prop="OperatorID" class="one-line-item">
          <el-select v-model="form.OperatorID" placeholder="操作员" disabled>
            <el-option v-for="(item, index) in userList" :key="index"
                       :label="`${item.UserID}-${item.RealName}`" :value="item.UserID">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="指廊" prop="AisleCode" class="one-line-item">
          <el-input v-model.trim="form.AisleCode" placeholder="指廊" disabled></el-input>
        </el-form-item>


        <el-form-item label="负责区域" prop="Area" class="one-line-item">
          <el-select v-model="form.Area" placeholder="请选择负责区域" multiple disabled>
            <el-option :label="item.BridgeCode" :value="item.BridgeCode"
                       v-for="(item,index) in AreaList" :key="index">
            </el-option>
          </el-select>
        </el-form-item>


        <el-form-item label="岗位属性" prop="Attribute" class="one-line-item">
          <el-select v-model="form.Attribute" placeholder="请选择岗位属性" disabled>
            <el-option label="固定" value="regular"></el-option>
            <el-option label="机动" value="reserve"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="上班时间" prop="StartTime" class="one-line-item">
          <el-date-picker v-model="form.StartTime" type="datetime"
                          placeholder="选择上班时间"
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="下班时间" prop="StopTime" class="one-line-item">
          <el-date-picker v-model="form.StopTime" type="datetime"
                          placeholder="选择下班时间"
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="调度员" prop="DispatcherID" class="one-line-item">
          <el-input v-model.trim="form.DispatcherID" placeholder="调度员" disabled></el-input>
        </el-form-item>

      </el-form>

      <el-button type="primary" class="from-confirm-btn" @click="submit">
        确定
      </el-button>
    </div>

  </el-dialog>
</template>
<script>
  import {mapState} from 'vuex'
  import {GetDateFormat} from '../../../utils/public'
  import {dispatchService} from '../../../http/service'
  import {editSchedulingField, initSchedulingField} from '../../../http/filed'

  export default {
    props: ['getScheduling', 'userList'],
    computed: {
      ...mapState(['editDialog', 'row'])
    },
    data() {
      return {
        form: {
          Area: []
        },
        AreaList: [],
        rules: {
          SubmitTime: [
            {required: false, message: "选择排班时间", trigger: "change"}
          ],
          OperatorID: [
            {required: true, message: "请选择操作员", trigger: "blur"}
          ],
          AisleCode: [
            {required: true, message: "请选择指廊", trigger: "blur"}
          ],
          Area: [
            {type: 'array', required: true, message: "请选择负责区域", trigger: "change"}
          ],
          Attribute: [
            {required: true, message: "请选择岗位属性", trigger: "blur"}
          ],
          StartTime: [
            {required: true, message: "选择上班时间", trigger: "change"}
          ],
          StopTime: [
            {required: true, message: "选择下班时间", trigger: "change"}
          ],
          DispatcherID: [
            {required: true, message: "选择输入调度员", trigger: "blur"}
          ]
        }
      }
    },
    methods: {
      init(row) {
        this.form = {
          ID: row.ID,
          ScheduleNumber: row.ScheduleNumber,
          OperatorID: row.OperatorID,
          AisleCode: row.AisleCode,
          Area: row.Area.split(','),
          Attribute: row.Attribute,
          StartTime: GetDateFormat(row.StartTime, 'yyyy-mm-dd HH:MM:ss'),
          StopTime: GetDateFormat(row.StopTime, 'yyyy-mm-dd HH:MM:ss'),
          DispatcherID: row.DispatcherID + ''
        }
        this.getAisleCodeAndArea()
      },
      // 1、获取操作员对应的指廊和负责区域
      getAisleCodeAndArea() {
        dispatchService.initScheduling({
          params: initSchedulingField(this.form.OperatorID),
          cb: (res) => {
            this.AreaList = res
          }
        })
      },
      // 1、关闭窗口1
      beforeClose() {
        this.setEditDialog(false)
      },
      // 2、关闭窗口2
      setEditDialog(editDialog) {
        this.$store.dispatch('setEditDialog', editDialog)
      },
      // 3、关闭窗口3
      close() {
        this.form = {Area: []}
        this.$store.dispatch('setRow', null)
        this.setEditDialog(false)
      },
      submit() {
        this.$refs.form.validate((valid) => {
          if (valid) {
            dispatchService.editScheduling({
              params: editSchedulingField(this.form),
              cb: (res) => {
                this.getScheduling()
                this.setEditDialog(false)
              }
            })
          }
        })
      }
    },
    watch: {
      row() {
        if (this.row) {
          this.init(this.row)
        }
      }
    }
  }
</script>
