<template>
  <div class="mobile-devices-layout">
    <div class="mobile-devices-header">
      <el-button type="text" class="text-btn">
        <i class="back"></i>
        返回首页
      </el-button>
    </div>
    <div class="content">
      <div class="search-common">
        <div class="search-top">

          <div class="search-item">
            <span class="txt">设备品牌</span>
            <el-input v-model="searchForm.input" placeholder="设备品牌"></el-input>
          </div>

          <div class="search-item">
            <span class="txt">设备型号</span>
            <el-select v-model="searchForm.command" placeholder="请选择设备型号">
              <el-option label="001" value="001"></el-option>
            </el-select>
          </div>

          <div class="search-item">
            <span class="txt">管理部门</span>
            <el-select v-model="searchForm.command" placeholder="请选择管理部门">
              <el-option label="001" value="001"></el-option>
            </el-select>
          </div>

        </div>
        <div class="search-bottom">

          <div class="search-item">
            <span class="txt">责任人</span>
            <el-select v-model="searchForm.command" placeholder="请选择责任人">
              <el-option label="001" value="001"></el-option>
            </el-select>
          </div>

          <div class="search-item">
            <span class="txt">可用状态</span>
            <el-select v-model="searchForm.command" placeholder="请选择可用状态">
              <el-option label="001" value="001"></el-option>
            </el-select>
          </div>

          <el-button type="primary" class="search-btn">查询</el-button>
        </div>
      </div>


      <div class="table-header">
        <h2 class="title">移动设备管理</h2>
        <el-button type="primary" class="table-btn1" icon="el-icon-plus">新增</el-button>
        <el-button type="primary" class="table-btn2" icon="el-icon-delete">删除</el-button>
      </div>

      <div class="table-content">
        <div class="table-common">
          <el-table :data="tableData" border height="390px">
            <el-table-column type="selection" width="40px" align="center"></el-table-column>
            <el-table-column v-for="(item,index) in tableItem" :key="index"
                             :prop="item.prop" :label="item.name"
                             align="center"  :min-width="item.width">

            </el-table-column>
            <el-table-column label="使用记录" align="center" width="90px">
              <template slot-scope="scope">
                <el-button type="primary" class="table-handle-btn3" @click="edit(scope.row)">查询</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="table-sign">
          <div class="page-handle h100">
            <el-pagination
                background small
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="page.num" :page-size="page.size"
                layout="prev, pager, next, jumper"
                :total="page.total">
            </el-pagination>
            <el-button class="page-handle-btn" @click="handleCurrentChange">确定</el-button>
          </div>
        </div>
      </div>
    </div>
    <editBoarding></editBoarding>
    <boardingBill></boardingBill>
  </div>
</template>
<script>
  import {editBoarding, boardingBill} from '../boarding/pages/index'
  import {dispatchService} from '../../http/service'
  import {getMobileDevicesField} from '../../http/filed'

  export default {
    components: {editBoarding, boardingBill},
    data() {
      return {
        searchForm: {},
        tableItem: [
          {prop: "date1", name: "序号", width: "6%"},
          {prop: "date3", name: "设备编号", width: "10%"},
          {prop: "date3", name: "设备品牌", width: "10%"},
          {prop: "date3", name: "设备型号", width: "10%"},
          {prop: "date5", name: "管理部门", width: "10%"},
          {prop: "date2", name: "责任人", width: "10%"},
          {prop: "date3", name: "设备IMEI编号", width: "10%"},
          {prop: "date3", name: "物联网卡号", width: "10%"},
          {prop: "date4", name: "可用状态", width: "10%"}
        ],
        tableData: [],
        isChecked: true,
        page: {num: 1, size: 10, total: 1000}
      }
    },
    mounted() {
      this.onReady()
    },
    methods: {
      // 1、初始化数据
      onReady() {
        // 2、移动设备管理数据
        this.getMobileDevices()
      },
      // 2、设备管理数据
      getMobileDevices() {
        dispatchService.getMobileDevices({
          params: getMobileDevicesField(this.searchForm, this.page),
          cb: (res) => {
            console.log(res);
            this.tableData = res.List
            this.page.total=res.TotalCount
            this.page.PageSize=res.PageSize
          }
        })
      },
      toBill(row) {
        this.$store.dispatch('setDialog', true)
      },
      edit(row) {
        this.$store.dispatch('setEditDialog', true)
      },
      toNotice(boolean) {
        this.isChecked = boolean
        this.$store.dispatch('setDialog', true)
      },
      handleSizeChange(val) {

      },
      handleCurrentChange(val) {

      }
    }
  }
</script>
<style lang="less" scoped>
  .mobile-devices-layout {
    .mobile-devices-header {
      display: flex;
      align-items: center;
      height: 50px;
      background: #fff;
      border-top: 1px solid #f0f1f3;
      position: relative;

      .text-btn {
        position: absolute;
        right: 45px;
        color: #a9b3bf;

        .back {
          display: inline-block;
          width: 16px;
          height: 16px;
          background: url("../../assets/img/back.png");
          vertical-align: middle;
          margin-right: 5px;
        }
      }
    }

    .content {

      .table-header {
        .title {
          font-size: 18px;
          margin-left: 25px;
        }

        .title {
          font-size: 18px;
          margin-left: 25px;
        }

        .table-btn1 {
          position: absolute;
          right: 120px;
        }

        .table-btn2 {
          position: absolute;
          right: 34px;
          background: #2bcde6;
          border-color: #2bcde6;
        }
      }
      .table-sign{
        justify-content: center;
      }
    }
  }
</style>
