<template>
  <el-dialog :visible="addDialog" @close="close"
             custom-class="common-dialog"
             width="630" :before-close="beforeClose">
    <div class="dialog-header">
      <i class="el-icon-caret-right icon"></i>

      <h2 class="title">新增</h2>

      <img src="../../../assets/img/close.png" class="close" @click="close">

    </div>
    <div class="dialog-form">
      <el-form label-width="100px" :status-icon="true" :model="form"
               :rules="rules" ref="form">

        <el-form-item label="姓名" prop="RealName" class="data-item">
          <el-input v-model.trim="form.data1" placeholder="请输入姓员"></el-input>
        </el-form-item>

        <el-form-item label="员工号" prop="UserCode" class="one-line-item">
          <el-input v-model.trim="form.data1" placeholder="请输入员工员"></el-input>
        </el-form-item>

        <el-form-item label="登录密码" prop="Password" class="one-line-item">
          <el-input v-model.trim="form.data1" placeholder="请输入登录密码"></el-input>
        </el-form-item>

        <el-form-item label="手机号码" prop="Mobile" class="one-line-item">
          <el-input v-model.trim="form.data1" placeholder="请输入手机号"></el-input>
        </el-form-item>

        <el-form-item label="部门" prop="DepartID" class="data-item">
          <el-select v-model="form.command" placeholder="请选择部门">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="职位" prop="JobID" class="one-line-item">
          <el-select v-model="form.command" placeholder="请选择职位">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>
    
      </el-form>

      <el-button type="primary" class="from-confirm-btn">
        确定
      </el-button>
      <el-button type="" class="from-confirm-btn">
        取消
      </el-button>
    </div>

  </el-dialog>
</template>
<script>
  import {mapState} from 'vuex'

  export default {
    props: ['isChecked'],
    computed: {
      ...mapState(['addDialog'])
    },
    data() {
      return {
        form: {},
        rules: {
          title: [
            {required: true, message: "请输入小于25个字标题", max: 25, trigger: "blur"}
          ],
          op_company_id: [
            {required: true, message: "请选择租户", trigger: "change"}
          ],
          picture: [
            {required: true, message: "请上传图片", trigger: "change"}
          ],
          link_address: [
            {required: true, message: "请填写链接", trigger: "blur"}
          ]
        }
      }
    },
    methods: {
      // 1、关闭窗口1
      beforeClose() {
        this.setAddDialog(false)
      },
      // 2、关闭窗口2
      setAddDialog(addDialog) {
        this.$store.dispatch('setAddDialog', addDialog)
      },
      // 3、关闭窗口3
      close() {
        this.setAddDialog(false)
      }
    },
    watch: {
      isChecked() {
        this.checked = this.isChecked
      }
    }
  }
</script>
<style scoped lang="less">
  .common-dialog {

  }
</style>
