<template>
  <div class="dispatch-layout">
    <div class="dispatch-header">
      <h2 class="title">当前部门：总调度</h2>
      <el-button type="text" class="text-btn">
        <i class="back"></i>
        返回首页
      </el-button>
    </div>

    <div class="content">
      <div class="search-common">
        <div class="search-top">

          <div class="search-item">
            <span class="txt">部门</span>
            <el-select v-model="searchForm.command" placeholder="请选择部门">
              <el-option label="001" value="001"></el-option>
            </el-select>
          </div>

          <div class="search-item">
            <span class="txt">指廊</span>
            <el-select v-model="searchForm.command" placeholder="请选择指廊">
              <el-option label="001" value="001"></el-option>
            </el-select>
          </div>

          <div class="search-item">
            <span class="txt">桥位号</span>
            <el-select v-model="searchForm.command" placeholder="请选择桥位号">
              <el-option label="001" value="001"></el-option>
            </el-select>
          </div>

          <div class="search-item">
            <span class="txt">时间</span>
            <el-date-picker v-model="searchForm.time" type="date" placeholder="时间"></el-date-picker>
            <span class="icon">—</span>
            <el-date-picker v-model="searchForm.time" type="date" placeholder="时间"></el-date-picker>
          </div>


        </div>
        <div class="search-bottom">
          <div class="search-item">
            <span class="txt">状态</span>
            <el-select v-model="searchForm.command" placeholder="请选择状态">
              <el-option label="001" value="001"></el-option>
            </el-select>
          </div>

          <div class="search-btn-item">
            <el-button type="primary" class="sign-btn">未处理</el-button>
            <el-button type="primary" class="sign-btn sign-btn1">已接受</el-button>
            <el-button type="primary" class="sign-btn sign-btn2">已拒绝</el-button>
            <el-button type="primary" class="sign-btn sign-btn3">程序标注完成</el-button>
            <el-button type="primary" class="sign-btn sign-btn4">APP标注完成</el-button>
            <el-button type="primary" class="sign-btn sign-btn5">调度标注完成</el-button>
            <el-button type="primary" class="sign-btn sign-btn6">任务取消</el-button>
          </div>

          <el-button type="primary" class="search-btn" @click="getDispatch">查询</el-button>
        </div>
      </div>

      <div class="table-header">
        <h2 class="title">航班调度</h2>
        <el-button type="primary" class="table-btn1">历史纪录</el-button>
        <el-button type="primary" class="table-btn2" icon="el-icon-download">导出</el-button>
      </div>

      <div class="table-content">
        <div class="table-common">
          <el-table :data="tableData" border height="390px" :row-class-name="tableRowClassName">
            <el-table-column v-for="(item,index) in tableItem" :key="index"
                             :prop="item.prop" :label="item.name" align="center" :min-width="item.width">
              <template slot-scope="scope">
                <div v-if="item.prop === 'BriDutyState'">
                  <span v-show="scope.row.BriDutyState===0">未到岗</span>
                  <span v-show="scope.row.BriDutyState===1">到岗</span>
                </div>
                <div v-else-if="item.prop === 'FinishMark'">
                  <span v-show="scope.row.FinishMark===0">未处理</span>
                  <span v-show="scope.row.FinishMark===1">已接受</span>
                  <span v-show="scope.row.FinishMark===2">已拒绝</span>
                  <span v-show="scope.row.FinishMark===3">程序标注完成</span>
                  <span v-show="scope.row.FinishMark===4">APP标注完成</span>
                  <span v-show="scope.row.FinishMark===5">调度标注完成</span>
                  <span v-show="scope.row.FinishMark===6">任务取消</span>
                </div>
                <div v-else>{{scope.row[item.prop]}}</div>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="center" width="220px" min-width="14%">
              <template slot-scope="scope">
                <el-button type="primary" class="table-handle-btn1" @click="audit(scope.row)">审计</el-button>
                <el-button type="primary" class="table-handle-btn2" @click="remark(scope.row)">转交</el-button>
                <el-button type="primary" class="table-handle-btn3" @click="remark(scope.row)">标注</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="table-sign">
          <div class="bell" @click="toNotice(true)">
            <i class="fa fa-bell"></i>
            <span class="num">4</span>
          </div>

          <div class="comment" @click="toNotice(false)">
            <i class="el-icon-s-comment"></i>
            <span class="num">4</span>
          </div>
        </div>
      </div>
    </div>
    <dispatchNotice :isChecked="isChecked"></dispatchNotice>
  </div>
</template>
<script>
  import dispatchNotice from './pages/dispatchNotice'
  import {dispatchService} from '../../http/service'
  import {getDispatchField, auditField, remarkField} from '../../http/filed'

  export default {
    components: {dispatchNotice},
    data() {
      return {
        searchForm: {},
        tableItem: [
          {prop: "BridgeCode", name: "桥位号", width: "6%"},
          {prop: "FlightID", name: "航班", width: "6%"},
          {prop: "FlightNumber", name: "航班号", width: "6%"},
          {prop: "AircraftNumber", name: "飞机号", width: "6%"},
          {prop: "FlightState", name: "航班动态", width: "6%"},
          {prop: "RouteSegmentCn", name: "航段", width: "6%"},
          {prop: "TagDetail", name: "航班调度指令", width: "8%"},
          {prop: "OperatorName", name: "操作员", width: "6%"},
          {prop: "FinishMark", name: "指令反馈", width: "6%"},
          {prop: "BriDutyState", name: "人员到岗情况", width: "6%"},
          {prop: "BriStartTime", name: "靠桥时间", width: "8%"},
          {prop: "BriStopTime", name: "撤桥时间", width: "8"},
          {prop: "ParkingTime", name: "泊位时间", width: "8%"}
        ],
        tableData: [],
        isChecked: true,
      }
    },
    mounted() {
      this.onReady()
    },
    methods: {
      // 1、初始化数据
      onReady() {
        // 2、获取航班调度数据
        this.getDispatch()
      },
      // 2、获取航班调度数据
      getDispatch() {
        dispatchService.getDispatch({
          params: getDispatchField(this.searchForm),
          cb: (res) => {
            console.log(res);
            this.tableData = res.List
          }
        })
      },
      // 3、航班调度审计
      audit(row) {
        dispatchService.audit({
          params: auditField(row),
          cb: (res) => {
            console.log(res);
          }
        })
      },
      // 4、航班调度标注
      remark(row) {
        dispatchService.remark({
          params: remarkField(row),
          cb: (res) => {
            console.log(res);
          }
        })
      },
      // 2、获取航班调度数据
      tableRowClassName({row, rowIndex}) {
        if (row.FinishMark === 0) {
          return 'table-color0';
        } else if (row.FinishMark === 1) {
          return 'table-color1';
        } else if (row.FinishMark === 2) {
          return 'table-color2';
        } else if (row.FinishMark === 3) {
          return 'table-color3';
        } else if (row.FinishMark === 4) {
          return 'table-color4';
        } else if (row.FinishMark === 5) {
          return 'table-color5';
        } else if (row.FinishMark === 6) {
          return 'table-color6';
        }
        return '';
      },
      toNotice(boolean) {
        this.isChecked = boolean
        this.$store.dispatch('setDialog', true)
      }
    }
  }
</script>
<style lang="less" scoped>
  .dispatch-layout {
    .dispatch-header {
      display: flex;
      align-items: center;
      height: 50px;
      background: #fff;
      border-top: 1px solid #f0f1f3;
      position: relative;

      .title {
        display: inline-block;
        font-size: 18px;
        margin: 0 50px;
      }

      .text-btn {
        position: absolute;
        right: 45px;
        color: #a9b3bf;

        .back {
          display: inline-block;
          width: 16px;
          height: 16px;
          background: url("../../assets/img/back.png");
          vertical-align: middle;
          margin-right: 5px;
        }
      }
    }

    .content {
      .search-common {
        .search-btn-item {
          display: inline-block;
          margin-left: 70px;

          .sign-btn {
            margin-left: 20px;
            color: #000;
            background: #f9f8fd;
            border-color: #c9c9c9;
          }

          .sign-btn1 {
            background: #f6ea68;
            border-color: #f6ea68;
          }

          .sign-btn2 {
            background: #bdd7ee;
            border-color: #bdd7ee;
          }

          .sign-btn3 {
            background: #beeec8;
            border-color: #beeec8;
          }

          .sign-btn4 {
            background: #ffffff;
            border-color: #c9c9c9;
          }

          .sign-btn5 {
            background: #fbaab1;
            border-color: #fbaab1;
          }

          .sign-btn6 {
            background: #f5f5f5;
            border-color: #c9c9c9;
          }

        }
      }

      .table-header {
        .title {
          font-size: 18px;
          margin-left: 25px;
        }

        .table-btn1 {
          position: absolute;
          right: 124px;
          background: #2bcde6;
          border-color: #2bcde6;
        }

        .table-btn2 {
          position: absolute;
          right: 36px;
          background: #ebb133;
          border-color: #ebb133;
        }
      }

      .table-sign {
        position: relative;

        .bell {
          position: absolute;
          right: 90px;
          color: #da1a05;
          cursor: pointer;
        }

        .comment {
          position: absolute;
          right: 60px;
          color: #9a9a9a;
          cursor: pointer;
        }

        .num {
          position: absolute;
          bottom: 10px;
          left: 10px;
          width: 16px;
          height: 16px;
          font-size: 12px;
          line-height: 16px;
          text-align: center;
          color: #fff;
          background: #da1a05;
          border-radius: 50%;
        }
      }
    }
  }
</style>
