<template>
  <div class="dynamic-layout">
    <div class="dynamic-header">
      <el-radio-group v-model="checkBtn" class="check-btn" @change="checkChange">
        <el-radio-button :label="true">单屏</el-radio-button>
        <el-radio-button :label="false">双屏</el-radio-button>
      </el-radio-group>

      <el-button type="primary" class="state-btn">自动滚屏中</el-button>
      <div class="set-content" v-show="isCheck&&checkBtn">
        <el-button type="primary" class="set-btn" @click="setTableItemBox1=!setTableItemBox1">自定义显示字段
        </el-button>
        <div class="set-box" v-show="setTableItemBox1">
          <el-checkbox-group v-model="tableItemCheck1" class="set-list">
            <el-checkbox class="set-item" v-for="(item,index) in tableItem" :key="index" :label="item.prop">
              {{item.name}}
            </el-checkbox>
          </el-checkbox-group>
          <div class="set-bottom">
            <el-button type="primary" class="set-bottom-btn" @click="confirmChange1">确定</el-button>
          </div>
        </div>
      </div>

      <div class="set-content" v-show="!isCheck&&checkBtn">
        <el-button type="primary" class="set-btn" @click="setTableItemBox2=!setTableItemBox2">自定义显示字段
        </el-button>
        <div class="set-box" v-show="setTableItemBox2">
          <el-checkbox-group v-model="tableItemCheck2" class="set-list">
            <el-checkbox class="set-item" v-for="(item,index) in tableItem" :key="index" :label="item.prop">
              {{item.name}}
            </el-checkbox>
          </el-checkbox-group>
          <div class="set-bottom">
            <el-button type="primary" class="set-bottom-btn" @click="confirmChange2">确定</el-button>
          </div>
        </div>
      </div>


      <el-button type="text" class="text-btn">
        <i class="back"></i>
        返回首页
      </el-button>
    </div>
    <div class="content clearfix" :class="{'content-double':!checkBtn}">
      <div class="content-left" v-show="isCheck||!checkBtn">
        <div class="search-common">
          <div class="search-top">

            <div class="search-item">
              <span class="txt">指廊</span>
              <el-select v-model="searchForm1.command" placeholder="请选择指廊">
                <el-option label="001" value="001"></el-option>
              </el-select>
            </div>

            <div class="search-item">
              <span class="txt">航班号</span>
              <el-input v-model="searchForm1.FlightNumber" placeholder="航班号"></el-input>
            </div>

            <div class="search-item">
              <span class="txt">机位号</span>
              <el-input v-model="searchForm1.StandCode" placeholder="机位号"></el-input>
            </div>

            <el-button type="primary" class="search-btn" @click="getDynamic(searchForm1, 'A')">查询</el-button>

          </div>
          <div class="search-bottom">
            <div class="search-item">
              <span class="txt">时间类型</span>
              <el-select v-model="searchForm1.command" placeholder="请选择时间类型">
                <el-option label="001" value="001"></el-option>
              </el-select>
            </div>

            <div class="search-item">
              <span class="txt">时间</span>
              <el-date-picker v-model="searchForm1.EstimatedServiceTime" type="date" placeholder="时间"></el-date-picker>
              <span class="icon">—</span>
              <el-date-picker v-model="searchForm1.LandTime " type="date" placeholder="时间"></el-date-picker>
            </div>
          </div>
        </div>

        <div class="dynamic-check" v-show="checkBtn">
          <el-button :type="isCheck?'primary':''" class="check-btn" @click="isCheck=true">进港航班</el-button>
          <el-button :type="!isCheck?'primary':''" @click="isCheck=false">出港航班</el-button>
          <el-input v-model="time" class="time-inp" disabled></el-input>
        </div>

        <div class="dynamic-no-check" v-show="!checkBtn">
          <span class="txt">进港航班动态</span>
          <el-input v-model="time" class="time-inp" disabled></el-input>
          <div class="set-content">
            <el-button type="primary" class="set-btn" @click="setTableItemBox1=!setTableItemBox1">自定义显示字段
            </el-button>
            <div class="set-box" v-show="setTableItemBox1">
              <el-checkbox-group v-model="tableItemCheck1" class="set-list">
                <el-checkbox class="set-item" v-for="(item,index) in tableItem" :key="index" :label="item.prop">
                  {{item.name}}
                </el-checkbox>
              </el-checkbox-group>
              <div class="set-bottom">
                <el-button type="primary" class="set-bottom-btn" @click="confirmChange1">确定</el-button>
              </div>
            </div>
          </div>
        </div>

        <div class="table-content">
          <div class="table-common">
            <el-table :data="tableData1" border height="390px" :row-class-name="tableRowClassName">
              <el-table-column v-for="(item,index) in tableItemList1" :key="index" :prop="item.prop"
                               :label="item.name" align="center" :min-width="item.width">
              </el-table-column>
            </el-table>
          </div>
          <div class="table-sign">
            <el-button type="primary" class="sign-btn sign-btn1">vip航班</el-button>
            <el-button type="primary" class="sign-btn sign-btn2">前站起飞</el-button>
            <el-button type="primary" class="sign-btn sign-btn3">15分钟到达</el-button>
            <el-button type="primary" class="sign-btn sign-btn4">已落地</el-button>
            <el-button type="primary" class="sign-btn sign-btn5">异常航班</el-button>
            <el-button type="primary" class="sign-btn sign-btn6">计划中</el-button>
          </div>
        </div>
      </div>

      <div class="content-right" v-show="!isCheck||!checkBtn">
        <div class="search-common">
          <div class="search-top">

            <div class="search-item">
              <span class="txt">指廊</span>
              <el-select v-model="searchForm2.command" placeholder="请选择指廊">
                <el-option label="001" value="001"></el-option>
              </el-select>
            </div>

            <div class="search-item">
              <span class="txt">航班号</span>
              <el-input v-model="searchForm2.input" placeholder="航班号"></el-input>
            </div>

            <div class="search-item">
              <span class="txt">机位号</span>
              <el-input v-model="searchForm2.input" placeholder="机位号"></el-input>
            </div>

            <el-button type="primary" class="search-btn" @click="getDynamic(searchForm2, 'B')">查询</el-button>

          </div>
          <div class="search-bottom">
            <div class="search-item">
              <span class="txt">时间类型</span>
              <el-select v-model="searchForm2.command" placeholder="请选择时间类型">
                <el-option label="001" value="001"></el-option>
              </el-select>
            </div>

            <div class="search-item">
              <span class="txt">时间</span>
              <el-date-picker v-model="searchForm2.time" type="date" placeholder="时间"></el-date-picker>
              <span class="icon">—</span>
              <el-date-picker v-model="searchForm2.time" type="date" placeholder="时间"></el-date-picker>
            </div>
          </div>
        </div>

        <div class="dynamic-check" v-show="checkBtn">
          <el-button :type="isCheck?'primary':''" class="check-btn" @click="isCheck=true">进港航班</el-button>
          <el-button :type="!isCheck?'primary':''" @click="isCheck=false">出港航班</el-button>
          <el-input v-model="time" class="time-inp" disabled></el-input>
        </div>

        <div class="dynamic-no-check" v-show="!checkBtn">
          <span class="txt">进港航班动态</span>
          <el-input v-model="time" class="time-inp" disabled></el-input>
          <div class="set-content">
            <el-button type="primary" class="set-btn" @click="setTableItemBox2=!setTableItemBox2">自定义显示字段
            </el-button>
            <div class="set-box" v-show="setTableItemBox2">
              <el-checkbox-group v-model="tableItemCheck2" class="set-list">
                <el-checkbox class="set-item" v-for="(item,index) in tableItem" :key="index" :label="item.prop">
                  {{item.name}}
                </el-checkbox>
              </el-checkbox-group>
              <div class="set-bottom">
                <el-button type="primary" class="set-bottom-btn" @click="confirmChange2">确定</el-button>
              </div>
            </div>
          </div>
        </div>

        <div class="table-content">
          <div class="table-common">
            <el-table :data="tableData2" border height="390px" :row-class-name="tableRowClassName">
              <el-table-column v-for="(item,index) in tableItemList2" :key="index" :prop="item.prop"
                               :label="item.name" align="center" :min-width="item.width">
              </el-table-column>
            </el-table>
          </div>
          <div class="table-sign">
            <el-button type="primary" class="sign-btn sign-btn1">vip航班</el-button>
            <el-button type="primary" class="sign-btn sign-btn2">前站起飞</el-button>
            <el-button type="primary" class="sign-btn sign-btn3">15分钟到达</el-button>
            <el-button type="primary" class="sign-btn sign-btn4">已落地</el-button>
            <el-button type="primary" class="sign-btn sign-btn5">异常航班</el-button>
            <el-button type="primary" class="sign-btn sign-btn6">计划中</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {newTime} from '../../utils/public'
  import {dispatchService} from '../../http/service'
  import {getDynamicField} from '../../http/filed'

  export default {
    data() {
      return {
        time: '',
        checkBtn: true, // 单双屏切换
        isCheck: true, // 进出港切换
        searchForm1: {},
        searchForm2: {},
        tableItem: [
          {prop: "StandCode", name: "机位号", width: "10%"},
          {prop: "FlightNumber", name: "航班号", width: "10%"},
          {prop: "AircraftType", name: "机型", width: "10%"},
          {prop: "AircraftNumber", name: "飞机号", width: "10%"},
          {prop: "FlightState", name: "航班状态", width: "10%"},
          {prop: "EstimatedServiceTime", name: "预计进港时间", width: "20%"},
          {prop: "LandTime", name: "落地时间", width: "20%"},
          {prop: "ExStatus", name: "异常状态", width: "10%"}
        ],
        setTableItemBox1: false,
        tableItemCheck1: [],
        tableItemList1: [],
        tableData1: [],
        setTableItemBox2: false,
        tableItemCheck2: [],
        tableItemList2: [],
        tableData2: []
      }
    },
    mounted() {
      this.onReady()
    },
    methods: {
      // 1、初始化数据
      onReady() {
        newTime((time) => {
          this.time = time
        }, 'yyyy-mm-dd HH:MM:ss')
        this.setTableItemList1()
        this.setTableItemList2()
        // 2、获取进港航班数据
        this.getDynamic(this.searchForm1, 'A')
        // 3、获取出港航班数据
        this.getDynamic(this.searchForm2, 'B')
        let timer = setInterval(() => {
          // 2、获取进港航班数据
          this.getDynamic(this.searchForm1, 'A')
          // 3、获取出港航班数据
          this.getDynamic(this.searchForm2, 'B')
        }, 5000)
        this.$router.beforeEach((to, from, next) => {
          clearInterval(timer)
          next()
        })
      },
      // 2、获取进港航班数据
      // 3、获取出港航班数据
      getDynamic(form, type) {
        dispatchService.getDynamic({
          params: getDynamicField(form, type),
          cb: (res) => {
            console.log(res);
            if (type === 'A') {
              this.tableData1 = res.List
            } else {
              this.tableData2 = res.List
            }
          }
        })
      },
      // 表格颜色样式显示
      tableRowClassName({row, rowIndex}) {
        if (row.ExStatus === '延误') {
          return 'table-color5';
        } else if (row.FlightState === 'VIP') {
          return 'table-color1';
        } else if (row.FlightState === '前站起飞') {
          return 'table-color2';
        } else if (row.FlightState === '15分钟到达') {
          return 'table-color3';
        } else if (row.FlightState === '已到达') {
          return 'table-color4';
        } else if (row.FlightState === '已离开') {
          return 'table-color4';
        } else if (row.FlightState === '计划中') {
          return 'table-color6';
        }
        return '';
      },
      setTableItemList1() {
        let tableItemList1 = JSON.parse(localStorage.getItem('tableItemList1'))
        if (tableItemList1) {
          this.tableItemList1 = tableItemList1
        } else {
          this.tableItemList1 = this.tableItem
          localStorage.setItem('tableItemList1', JSON.stringify(this.tableItem))
        }
        let arr = []
        this.tableItemList1.forEach((v, i) => {
          arr.push(v.prop)
        })
        this.tableItemCheck1 = arr
      },
      confirmChange1() {
        if (this.tableItemCheck1.length > 0) {
          let arr = []
          this.tableItem.forEach((v, i) => {
            this.tableItemCheck1.forEach((v1, i1) => {
              if (v1 === v.prop) {
                arr.push(v)
              }
            })
          })
          this.tableItemList1 = arr
          localStorage.setItem('tableItemList1', JSON.stringify(arr))
        } else {
          localStorage.removeItem('tableItemList1')
          this.setTableItemList1()
        }
        this.setTableItemBox1 = false
      },
      setTableItemList2() {
        let tableItemList2 = JSON.parse(localStorage.getItem('tableItemList2'))
        if (tableItemList2) {
          this.tableItemList2 = tableItemList2
        } else {
          this.tableItemList2 = this.tableItem
          localStorage.setItem('tableItemList2', JSON.stringify(this.tableItem))
        }
        let arr = []
        this.tableItemList2.forEach((v, i) => {
          arr.push(v.prop)
        })
        this.tableItemCheck2 = arr
      },
      confirmChange2() {
        if (this.tableItemCheck2.length > 0) {
          let arr = []
          this.tableItem.forEach((v, i) => {
            this.tableItemCheck2.forEach((v1, i1) => {
              if (v1 === v.prop) {
                arr.push(v)
              }
            })
          })
          this.tableItemList2 = arr
          localStorage.setItem('tableItemList2', JSON.stringify(arr))
        } else {
          localStorage.removeItem('tableItemList2')
          this.setTableItemList2()
        }
        this.setTableItemBox2 = false
      },
      checkChange() {
        console.log(this.checkBtn)
      }
    }
  }
</script>
<style lang="less" scoped>
  .dynamic-layout {
    .dynamic-header {
      display: flex;
      align-items: center;
      height: 50px;
      background: #fff;
      border-top: 1px solid #f0f1f3;
      position: relative;

      .check-btn {
        margin-left: 40px;
      }

      .state-btn {
        margin-left: 70px;
        background: #11b18f;
        border: 0;
      }


      .set-content {
        position: absolute;
        right: 180px;
        z-index: 10;

        .set-btn {
          background: #b2ceff;
          border: 0;
        }

        .set-box {
          position: absolute;
          top: 30px;
          right: 0;
          width: 230px;
          height: 230px;
          background: #fff;
          border: 1px solid #d6d6d6;
