            border-top: 1px solid #d6d6d6;
            display: flex;
            align-items: center;
            position: absolute;
            bottom: 0;
            width: 100%;

            .set-bottom-btn {
              margin-left: 15px;
            }
          }
        }
      }


      .text-btn {
        position: absolute;
        right: 45px;
        color: #a9b3bf;

        .back {
          display: inline-block;
          width: 16px;
          height: 16px;
          background: url("../../assets/img/back.png");
          vertical-align: middle;
          margin-right: 5px;
        }
      }
    }

    .content {
      .dynamic-check {
        height: 46px;
        background: #f5f4f9;
        margin-top: 20px;
        border: 1px solid #eaeaea;
        display: flex;
        align-items: center;
        position: relative;

        .check-btn {
          margin-left: 24px;
        }

        .time-inp {
          width: 150px;
          position: absolute;
          right: 25px;
        }
      }

      .dynamic-no-check {
        height: 46px;
        background: #f5f4f9;
        margin-top: 20px;
        border: 1px solid #eaeaea;
        display: flex;
        align-items: center;
        position: relative;

        .txt {
          font-size: 15px;
          font-weight: bold;
          margin: 0 20px;
        }

        .time-inp {
          width: 150px;
        }

        .set-content {
          position: absolute;
          right: 25px;
          z-index: 10;

          .set-btn {
            background: #b2ceff;
            border: 0;
          }

          .set-box {
            position: absolute;
            top: 30px;
            right: 0;
            width: 230px;
            height: 230px;
            background: #fff;
            border: 1px solid #d6d6d6;
            padding-bottom: 46px;

            .set-list {
              overflow: auto;
              height: 100%;

              .set-item {
                display: flex;
                align-items: center;
                height: 28px;
                padding-left: 15px;
                margin: 0;
              }

              .set-item:hover {
                background: #ebebeb;
              }
            }

            .set-bottom {
              height: 46px;
              border-top: 1px solid #d6d6d6;
              display: flex;
              align-items: center;
              position: absolute;
              bottom: 0;
              width: 100%;

              .set-bottom-btn {
                margin-left: 15px;
              }
            }
          }
        }
      }

      .table-content {
        .table-sign {
          height: 96px;
          border: 1px solid #e2e2e2;
          display: flex;
          align-items: center;

          .sign-btn {
            margin-left: 20px;
            color: #000;
          }

          .sign-btn1 {
            background: #f6ea68;
            border-color: #f6ea68;
          }

          .sign-btn2 {
            background: #bdd7ee;
            border-color: #bdd7ee;
          }

          .sign-btn3 {
            background: #beeec8;
            border-color: #beeec8;
          }

          .sign-btn4 {
            background: #ffffff;
            border-color: #c9c9c9;
          }

          .sign-btn5 {
            background: #fbaab1;
