      }
    }

    .content-double {
      .content-left {
        float: left;
        width: 50%;
        padding-right: 5px;
      }

      .content-right {
        float: left;
        width: 50%;
        padding-left: 5px;
      }

      .search-common {
        .search-item {
          .txt {
            display: none;
