<template>
  <el-dialog :visible="editDialog" @close="close"
             custom-class="common-dialog"
             width="630" :before-close="beforeClose">
    <div class="dialog-header">
      <i class="el-icon-caret-right icon"></i>

      <h2 class="title">编辑</h2>

      <img src="../../../assets/img/close.png" class="close" @click="close">

    </div>
    <div class="dialog-form">
      <el-form label-width="100px" :status-icon="true" :model="form"
               :rules="rules" ref="form">

        <el-form-item label="服务时间" prop="title" class="one-line-item">
          <el-date-picker v-model="form.time" type="datetime"
                          placeholder="选择服务时间"
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="设备类别" prop="title" class="data-item">
          <el-select v-model="form.command" placeholder="请选择设备类别">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="机位号" prop="title" class="data-item">
          <el-select v-model="form.command" placeholder="请选择机位号">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="航班号" prop="title" class="data-item">
          <el-select v-model="form.command" placeholder="请选择航班号">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="机型" prop="title" class="data-item">
          <el-select v-model="form.command" placeholder="请选择机型">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="飞机号" prop="title" class="data-item">
          <el-select v-model="form.command" placeholder="请选择飞机号">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="航段" prop="title" class="data-item">
          <el-select v-model="form.command" placeholder="请选择航段">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="飞机号" prop="title" class="data-item">
          <el-select v-model="form.command" placeholder="请选择飞机号">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="起飞标志" prop="title" class="data-item">
          <el-select v-model="form.command" placeholder="请选择起飞标志">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="操作员" prop="title" class="one-line-item">
          <el-select v-model="form.command" placeholder="请选择操作员">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>


        <el-form-item label="开始时间" prop="title" class="one-line-item">
          <el-date-picker v-model="form.time" type="datetime"
                          placeholder="选择开始时间"
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="结束时间" prop="title" class="one-line-item">
          <el-date-picker v-model="form.time" type="datetime"
                          placeholder="选择结束时间"
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="服务时长" prop="title" class="one-line-item">
          <el-date-picker v-model="form.time" type="datetime"
                          placeholder="选择服务时长"
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>

      <div class="from-btn">
        <el-button type="primary" class="confirm-btn">
          确认
        </el-button>
        <el-button class="cancel-btn">
          取消
        </el-button>
      </div>

    </div>

  </el-dialog>
</template>
<script>
  import {mapState} from 'vuex'

  export default {
    props: ['isChecked'],
    computed: {
      ...mapState(['editDialog'])
    },
    data() {
      return {
        form: {},
        rules: {
          title: [
            {required: true, message: "请输入小于25个字标题", max: 25, trigger: "blur"}
          ],
          op_company_id: [
            {required: true, message: "请选择租户", trigger: "change"}
          ],
          picture: [
            {required: true, message: "请上传图片", trigger: "change"}
          ],
          link_address: [
            {required: true, message: "请填写链接", trigger: "blur"}
          ]
        }
      }
    },
    methods: {
      // 1、关闭窗口1
      beforeClose() {
        this.setEditDialog(false)
      },
      // 2、关闭窗口2
      setEditDialog(editDialog) {
        this.$store.dispatch('setEditDialog', editDialog)
      },
      // 3、关闭窗口3
      close() {
        this.setEditDialog(false)
      }
    },
    watch: {
      isChecked() {
        this.checked = this.isChecked
      }
    }
  }
</script>
<style scoped lang="less">
  .common-dialog {

  }
</style>
