<template>
  <el-dialog :visible="deleteDialog" @close="close"
             custom-class="common-dialog"
             width="520" :before-close="beforeClose">
    <div class="dialog-header">
      <i class="el-icon-caret-right icon"></i>

      <h2 class="title">删除</h2>

      <img src="../../../assets/img/close.png" class="close" @click="close">

    </div>
    <div class="dialog-content">

      <p class="txt2">是否确认删除，</p>
      <p class="txt3">如若确认，将删除一切信息且不能恢复</p>
      <div class="dialog-btn2">
        <el-button type="primary" class="confirm-btn" @click="toDelete">
          确定
        </el-button>
        <el-button class="cancel-btn" @click="close">
          取消
        </el-button>
      </div>
    </div>

  </el-dialog>
</template>
<script>
  import {mapState} from 'vuex'
  import {dispatchService} from '../../../http/service'
  import {deleteSchedulingField} from '../../../http/filed'

  export default {
    props: ['getScheduling'],
    computed: {
      ...mapState(['deleteDialog', 'deleteRow'])
    },
    data() {
      return {
        ID: ''
      }
    },
    methods: {
      // 1、关闭窗口1
      beforeClose() {
        this.setDeleteDialog(false)
      },
      // 2、关闭窗口2
      setDeleteDialog(deleteDialog) {
        this.$store.dispatch('setDeleteDialog', deleteDialog)
      },
      // 3、关闭窗口3
      close() {
        this.$store.dispatch('setDeleteRow', null)
        this.setDeleteDialog(false)
      },
      toDelete(row) {
        dispatchService.deleteScheduling({
          params: deleteSchedulingField(this.ID),
          cb: (res) => {
            this.getScheduling()
            this.setDeleteDialog(false)
          }
        })
      }
    },
    watch: {
      deleteRow() {
        if (this.deleteRow) {
          console.log(this.deleteRow)
          this.ID = this.deleteRow
        }
      }
    }
  }
</script>
